.homePage {
    background-color: black;
}

.nameBabylon {
    width: 80%;
    height: 80%;
    top: 5%;
    left: 0%;
    position: absolute;
    outline: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}


.slogan {
    color: rgba(248, 248, 255, 0.822);
    position: absolute;
    top: 74%;
    left: 50%;
    transform: translate(-50%, 0);
    text-align: center;
    font-family: Verdana, sans-serif;
    font-size: 120%;
}

[data-ca3_icon]::before {
    font-weight: normal;
    content: attr(data-ca3_icon);
}

.scroll-arrow {
    background-image: url(data:image/svg+xml;base64,);
	background-size: contain;
	background-repeat: no-repeat;
    cursor:pointer;
	height: 60px;
	width: 80px;
	line-height: 60px;
	position: absolute;
    top: 90%;
	left: calc(50% - 40px);
    display:block;
	bottom: 0px;
	color: #FFF;
	text-align: center;
	font-size: 70px;
	z-index: 100;
	text-decoration: none;
	text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.4);

	-webkit-animation: ca3_fade_move_down 4s ease-in-out infinite;
	-moz-animation:    ca3_fade_move_down 4s ease-in-out infinite;
	animation:         ca3_fade_move_down 4s ease-in-out infinite;
}


/*animated scroll arrow animation*/
@-webkit-keyframes ca3_fade_move_down {
  0%   { -webkit-transform:translate(0,-10px); opacity: 0;  }
  50%  { opacity: 1;  }
  100% { -webkit-transform:translate(0,10px); opacity: 0; }
}
@-moz-keyframes ca3_fade_move_down {
  0%   { -moz-transform:translate(0,-10px); opacity: 0;  }
  50%  { opacity: 1;  }
  100% { -moz-transform:translate(0,10px); opacity: 0; }
}
@keyframes ca3_fade_move_down {
  0%   { transform:translate(0,-10px); opacity: 0;  }
  50%  { opacity: 1;  }
  100% { transform:translate(0,10px); opacity: 0; }
}
body {
    background-color:#000; overflow:hidden;
}

html body [data-ca3_iconfont="ETmodules"]::before {
    font-family: "ETmodules";
}